export default {
  props: {
    curTab: String
  },
  mounted () {
  },
  watch: {
  },
  methods: {
    goTab (text, tab) {
      if (tab === 'major') {
        this.$router.push({ name: 'major' })
        return
      }
      this.$emit('changeTab', tab)
      if (['prov', 'spec', 'class', 'categ'].indexOf(tab) !== -1) {
        this.title = '筛选' + text
      }
    }
  }
}



